export default "/**\r\n * atom-dark theme for `prism.js`\r\n * Based on Atom's `atom-dark` theme: https://github.com/atom/atom-dark-syntax\r\n * @author Joe Gibson (@gibsjose)\r\n */\r\n\r\ncode[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tcolor: #c5c8c6;\r\n\ttext-shadow: 0 1px rgba(0, 0, 0, 0.3);\r\n\tfont-family: Inconsolata, Monaco, Consolas, 'Courier New', Courier, monospace;\r\n\tdirection: ltr;\r\n\ttext-align: left;\r\n\twhite-space: pre;\r\n\tword-spacing: normal;\r\n\tword-break: normal;\r\n\tline-height: 1.5;\r\n\r\n\t-moz-tab-size: 4;\r\n\t-o-tab-size: 4;\r\n\ttab-size: 4;\r\n\r\n\t-webkit-hyphens: none;\r\n\t-moz-hyphens: none;\r\n\t-ms-hyphens: none;\r\n\thyphens: none;\r\n}\r\n\r\n/* Code blocks */\r\npre[class*=\"language-\"] {\r\n\tpadding: 1em;\r\n\tmargin: .5em 0;\r\n\toverflow: auto;\r\n\tborder-radius: 0.3em;\r\n}\r\n\r\n:not(pre) > code[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tbackground: #1d1f21;\r\n}\r\n\r\n/* Inline code */\r\n:not(pre) > code[class*=\"language-\"] {\r\n\tpadding: .1em;\r\n\tborder-radius: .3em;\r\n}\r\n\r\n.token.comment,\r\n.token.prolog,\r\n.token.doctype,\r\n.token.cdata {\r\n\tcolor: #7C7C7C;\r\n}\r\n\r\n.token.punctuation {\r\n\tcolor: #c5c8c6;\r\n}\r\n\r\n.namespace {\r\n\topacity: .7;\r\n}\r\n\r\n.token.property,\r\n.token.keyword,\r\n.token.tag {\r\n\tcolor: #96CBFE;\r\n}\r\n\r\n.token.class-name {\r\n\tcolor: #FFFFB6;\r\n\ttext-decoration: underline;\r\n}\r\n\r\n.token.boolean,\r\n.token.constant {\r\n\tcolor: #99CC99;\r\n}\r\n\r\n.token.symbol,\r\n.token.deleted {\r\n\tcolor: #f92672;\r\n}\r\n\r\n.token.number {\r\n\tcolor: #FF73FD;\r\n}\r\n\r\n.token.selector,\r\n.token.attr-name,\r\n.token.string,\r\n.token.char,\r\n.token.builtin,\r\n.token.inserted {\r\n\tcolor: #A8FF60;\r\n}\r\n\r\n.token.variable {\r\n\tcolor: #C6C5FE;\r\n}\r\n\r\n.token.operator {\r\n\tcolor: #EDEDED;\r\n}\r\n\r\n.token.entity {\r\n\tcolor: #FFFFB6;\r\n\tcursor: help;\r\n}\r\n\r\n.token.url {\r\n\tcolor: #96C