export default "/*\r\n * Synthwave '84 Theme originally by Robb Owen [@Robb0wen] for Visual Studio Code\r\n * Demo: https://marc.dev/demo/prism-synthwave84\r\n *\r\n * Ported for PrismJS by Marc Backes [@themarcba]\r\n */\r\n\r\ncode[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tcolor: #f92aad;\r\n\ttext-shadow: 0 0 2px #100c0f, 0 0 5px #dc078e33, 0 0 10px #fff3;\r\n\tbackground: none;\r\n\tfont-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;\r\n\tfont-size: 1em;\r\n\ttext-align: left;\r\n\twhite-space: pre;\r\n\tword-spacing: normal;\r\n\tword-break: normal;\r\n\tword-wrap: normal;\r\n\tline-height: 1.5;\r\n\r\n\t-moz-tab-size: 4;\r\n\t-o-tab-size: 4;\r\n\ttab-size: 4;\r\n\r\n\t-webkit-hyphens: none;\r\n\t-moz-hyphens: none;\r\n\t-ms-hyphens: none;\r\n\thyphens: none;\r\n}\r\n\r\n/* Code blocks */\r\npre[class*=\"language-\"] {\r\n\tpadding: 1em;\r\n\tmargin: .5em 0;\r\n\toverflow: auto;\r\n}\r\n\r\n:not(pre) > code[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tbackground-color: transparent !important;\r\n\tbackground-image: linear-gradient(to bottom, #2a2139 75%, #34294f);\r\n}\r\n\r\n/* Inline code */\r\n:not(pre) > code[class*=\"language-\"] {\r\n\tpadding: .1em;\r\n\tborder-radius: .3em;\r\n\twhite-space: normal;\r\n}\r\n\r\n.token.comment,\r\n.token.block-comment,\r\n.token.prolog,\r\n.token.doctype,\r\n.token.cdata {\r\n\tcolor: #8e8e8e;\r\n}\r\n\r\n.token.punctuation {\r\n\tcolor: #ccc;\r\n}\r\n\r\n.token.tag,\r\n.token.attr-name,\r\n.token.namespace,\r\n.token.number,\r\n.token.unit,\r\n.token.hexcode,\r\n.token.deleted {\r\n\tcolor: #e2777a;\r\n}\r\n\r\n.token.property,\r\n.token.selector {\r\n\tcolor: #72f1b8;\r\n\ttext-shadow: 0 0 2px #100c0f, 0 0 10px #257c5575, 0 0 35px #21272475;\r\n}\r\n\r\n.token.function-name {\r\n\tcolor: #6196cc;\r\n}\r\n\r\n.token.boolean,\r\n.token.selector .token.id,\r\n.token.function {\r\n\tcolor: #fdfdfd;\r\n\ttext-shadow: 0 0 2px #001716, 0 0 3px #03edf975, 0 0 5px #03edf975, 0 