export default "/**\r\n * GHColors theme by Avi Aryan (http://aviaryan.in)\r\n * Inspired by Github syntax coloring\r\n */\r\n\r\ncode[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tcolor: #393A34;\r\n\tfont-family: \"Consolas\", \"Bitstream Vera Sans Mono\", \"Courier New\", Courier, monospace;\r\n\tdirection: ltr;\r\n\ttext-align: left;\r\n\twhite-space: pre;\r\n\tword-spacing: normal;\r\n\tword-break: normal;\r\n\tfont-size: .9em;\r\n\tline-height: 1.2em;\r\n\r\n\t-moz-tab-size: 4;\r\n\t-o-tab-size: 4;\r\n\ttab-size: 4;\r\n\r\n\t-webkit-hyphens: none;\r\n\t-moz-hyphens: none;\r\n\t-ms-hyphens: none;\r\n\thyphens: none;\r\n}\r\n\r\npre > code[class*=\"language-\"] {\r\n\tfont-size: 1em;\r\n}\r\n\r\npre[class*=\"language-\"]::-moz-selection, pre[class*=\"language-\"] ::-moz-selection,\r\ncode[class*=\"language-\"]::-moz-selection, code[class*=\"language-\"] ::-moz-selection {\r\n\tbackground: #b3d4fc;\r\n}\r\n\r\npre[class*=\"language-\"]::selection, pre[class*=\"language-\"] ::selection,\r\ncode[class*=\"language-\"]::selection, code[class*=\"language-\"] ::selection {\r\n\tbackground: #b3d4fc;\r\n}\r\n\r\n/* Code blocks */\r\npre[class*=\"language-\"] {\r\n\tpadding: 1em;\r\n\tmargin: .5em 0;\r\n\toverflow: auto;\r\n\tborder: 1px solid #dddddd;\r\n\tbackground-color: white;\r\n}\r\n\r\n/* Inline code */\r\n:not(pre) > code[class*=\"language-\"] {\r\n\tpadding: .2em;\r\n\tpadding-top: 1px;\r\n\tpadding-bottom: 1px;\r\n\tbackground: #f8f8f8;\r\n\tborder: 1px solid #dddddd;\r\n}\r\n\r\n.token.comment,\r\n.token.prolog,\r\n.token.doctype,\r\n.token.cdata {\r\n\tcolor: #999988;\r\n\tfont-style: italic;\r\n}\r\n\r\n.token.namespace {\r\n\topacity: .7;\r\n}\r\n\r\n.token.string,\r\n.token.attr-value {\r\n\tcolor: #e3116c;\r\n}\r\n\r\n.token.punctuation,\r\n.token.operator {\r\n\tcolor: #393A34; /* no highlight */\r\n}\r\n\r\n.token.entity,\r\n.token.url,\r\n.token.symbol,\r\n.token.number,\r\n.token.boolean,\r\n.token.variable,\r\n.token.constant,\r\n.token.property,\r\n.token.regex,\r\n.token.inserted {\r\n\tcolor: #36acaa;\r\n}\r\n\r\n.token.atrule,\r\n.token.keyword,\r\n.token.attr-name,\r\n.language-autohotkey .token.selector {\r\n\tcolor: #00a4db;\r\n}\r\n\r\n.token.function,\r\n.token.deleted,\r\n.language-autohotkey .token.tag {\r\n\tcolor: #9a050f;\r\n}\r\n\r\n.token.tag,\r\n.token.selector,\r\n.language-autohotkey .token.keyword {\r\n\tcolor: #00009f;\r\n}\r\n\r\n.token.important,\r\n.token.function,\r\n.token.bold {\r\n\tfont-weight: bold;\r\n}\r\n\r\n.token.italic {\r\n\tfont-style: italic;\r\n}\r\n";