export default "/*\r\n * Based on Plugin: Syntax Highlighter CB\r\n * Plugin URI: http://wp.tutsplus.com/tutorials/plugins/adding-a-syntax-highlighter-shortcode-using-prism-js\r\n * Description: Highlight your code snippets with an easy to use shortcode based on Lea Verou's Prism.js.\r\n * Version: 1.0.0\r\n * Author: c.bavota\r\n * Author URI: http://bavotasan.comhttp://wp.tutsplus.com/tutorials/plugins/adding-a-syntax-highlighter-shortcode-using-prism-js/ */\r\n/* http://cbavota.bitbucket.org/syntax-highlighter/  */\r\n\r\n/* =====   ===== */\r\ncode[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tcolor: #fff;\r\n\ttext-shadow: 0 1px 1px #000;\r\n\tfont-family: Menlo, Monaco, \"Courier New\", monospace;\r\n\tdirection: ltr;\r\n\ttext-align: left;\r\n\tword-spacing: normal;\r\n\twhite-space: pre;\r\n\tword-wrap: normal;\r\n\tline-height: 1.4;\r\n\tbackground: none;\r\n\tborder: 0;\r\n\r\n\t-moz-tab-size: 4;\r\n\t-o-tab-size: 4;\r\n\ttab-size: 4;\r\n\r\n\t-webkit-hyphens: none;\r\n\t-moz-hyphens: none;\r\n\t-ms-hyphens: none;\r\n\thyphens: none;\r\n}\r\n\r\npre[class*=\"language-\"] code {\r\n\tfloat: left;\r\n\tpadding: 0 15px 0 0;\r\n}\r\n\r\npre[class*=\"language-\"],\r\n:not(pre) > code[class*=\"language-\"] {\r\n\tbackground: #222;\r\n}\r\n\r\n/* Code blocks */\r\npre[class*=\"language-\"] {\r\n\tpadding: 15px;\r\n\tmargin: 1em 0;\r\n\toverflow: auto;\r\n\t-moz-border-radius: 8px;\r\n\t-webkit-border-radius: 8px;\r\n\tborder-radius: 8px;\r\n}\r\n\r\n/* Inline code */\r\n:not(pre) > code[class*=\"language-\"] {\r\n\tpadding: 5px 10px;\r\n\tline-height: 1;\r\n\t-moz-border-radius: 3px;\r\n\t-webkit-border-radius: 3px;\r\n\tborder-radius: 3px;\r\n}\r\n\r\n.token.comment,\r\n.token.prolog,\r\n.token.doctype,\r\n.token.cdata {\r\n\tcolor: #797979;\r\n}\r\n\r\n.token.selector,\r\n.token.operator,\r\n.token.punctuation {\r\n\tcolor: #fff;\r\n}\r\n\r\n.token.namespace {\r\n\topacity: .7;\r\n}\r\n\r\n.token.tag,\r\n.token.boolean {\r\n\tcolor: #ffd893;\r\n}\r\n\r\n.token.atrule,\r\n.token.attr-value,\r\n.token.hex,\r\n.token.string {\r\n\tcolor: #B0C975;\r\n}\r\n\r\n.token.property,\r\n.token.entity,\r\n.token.url,\r\n.token.attr-name,\r\n.token.keyword {\r\n\tcolor: #c27628;\r\n}\r\n\r\n.token.regex {\r\n\tcolor: #9B71C6;\r\n}\r\n\r\n.token.entity {\r\n\tcursor: help;\r\n}\r\n\r\n.token.function,\r\n.token.constant {\r\n\tcolor: #e5a638;\r\n}\r\n\r\n.token.variable {\r\n\tcolor: #fdfba8;\r\n}\r\n\r\n.token.number {\r\n\tcolor: #8799B0;\r\n}\r\n\r\n.token.important,\r\n.token.deliminator {\r\n\tcolor: #E45734;\r\n}\r\n\r\n/* Line highlight plugin */\r\npre[data-line] {\r\n\tposition: relative;\r\n\tpadding: 1em 0 1em 3em;\r\n}\r\n\r\n.line-highlight {\r\n\tposition: absolute;\r\n\tleft: 0;\r\n\tright: 0;\r\n\tmargin-top: 1em; /* Same as .prism's padding-top */\r\n\tbackground: rgba(255, 255, 255, .2);\r\n\tpointer-events: none;\r\n\tline-height: inherit;\r\n\twhite-space: pre;\r\n}\r\n\r\n.line-highlight:before,\r\n.line-highlight[data-end]:after {\r\n\tcontent: attr(data-start);\r\n\tposition: absolute;\r\n\ttop: .3em;\r\n\tleft: .6em;\r\n\tmin-width: 1em;\r\n\tpadding: 0 .5em;\r\n\tbackground-color: rgba(255, 255, 255, .3);\r\n\tcolor: #fff;\r\n\tfont: bold 65%/1.5 sans-serif;\r\n\ttext-align: center;\r\n\t-moz-border-radius: 8px;\r\n\t-webkit-border-radius: 8px;\r\n\tborder-radius: 8px;\r\n\ttext-shadow: none;\r\n}\r\n\r\n.line-highlight[data-end]:after {\r\n\tcontent: attr(data-end);\r\n\ttop: auto;\r\n\tbottom: .4em;\r\n}\r\n\r\n/* for line numbers */\r\n.line-numbers-rows {\r\n\tmargin: 0;\r\n}\r\n\r\n.line-numbers-rows span {\r\n\tpadding-right: 10px;\r\n\tborder-right: 3px #d9d336 solid;\r\n}\r\n";