export default "/**\r\n * Darcula theme\r\n *\r\n * Adapted from a theme based on:\r\n * IntelliJ Darcula Theme (https://github.com/bulenkov/Darcula)\r\n *\r\n * @author Alexandre Paradis <service.paradis@gmail.com>\r\n * @version 1.0\r\n */\r\n\r\ncode[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tcolor: #a9b7c6;\r\n\tfont-family: Consolas, Monaco, 'Andale Mono', monospace;\r\n\tdirection: ltr;\r\n\ttext-align: left;\r\n\twhite-space: pre;\r\n\tword-spacing: normal;\r\n\tword-break: normal;\r\n\tline-height: 1.5;\r\n\r\n\t-moz-tab-size: 4;\r\n\t-o-tab-size: 4;\r\n\ttab-size: 4;\r\n\r\n\t-webkit-hyphens: none;\r\n\t-moz-hyphens: none;\r\n\t-ms-hyphens: none;\r\n\thyphens: none;\r\n}\r\n\r\npre[class*=\"language-\"]::-moz-selection, pre[class*=\"language-\"] ::-moz-selection,\r\ncode[class*=\"language-\"]::-moz-selection, code[class*=\"language-\"] ::-moz-selection {\r\n\tcolor: inherit;\r\n\tbackground: rgba(33, 66, 131, .85);\r\n}\r\n\r\npre[class*=\"language-\"]::selection, pre[class*=\"language-\"] ::selection,\r\ncode[class*=\"language-\"]::selection, code[class*=\"language-\"] ::selection {\r\n\tcolor: inherit;\r\n\tbackground: rgba(33, 66, 131, .85);\r\n}\r\n\r\n/* Code blocks */\r\npre[class*=\"language-\"] {\r\n\tpadding: 1em;\r\n\tmargin: .5em 0;\r\n\toverflow: auto;\r\n}\r\n\r\n:not(pre) > code[class*=\"language-\"],\r\npre[class*=\"language-\"] {\r\n\tbackground: #2b2b2b;\r\n}\r\n\r\n/* Inline code */\r\n:not(pre) > code[class*=\"language-\"] {\r\n\tpadding: .1em;\r\n\tborder-radius: .3em;\r\n}\r\n\r\n.token.comment,\r\n.token.prolog,\r\n.token.cdata {\r\n\tcolor: #808080;\r\n}\r\n\r\n.token.delimiter,\r\n.token.boolean,\r\n.token.keyword,\r\n.token.selector,\r\n.token.important,\r\n.token.atrule {\r\n\tcolor: #cc7832;\r\n}\r\n\r\n.token.operator,\r\n.token.punctuation,\r\n.token.attr-name {\r\n\tcolor: #a9b7c6;\r\n}\r\n\r\n.token.tag,\r\n.token.tag .punctuation,\r\n.token.doctype,\r\n.token.builtin {\r\n\tcolor: #e8bf6a;\r\n}\r\n\r\n.token.entity,\r\n.token.number,\r\n.token.symbol {\r\n\tcolor: #6897bb;\r\n}\r\n\r\n.token.property,\r\n.token.constant,\r\n.token.variable {\r\n\tcolor: #9876aa;\r\n}\r\n\r\n.token.string,\r\n.token.char {\r\n\tcolor: #6a8759;\r\n}\r\n\r\n.token.attr-value,\r\n.token.attr-value .punctuation {\r\n\tcolor: #a5c261;\r\n}\r\n\r\n.token.attr-value .punctuation:first-child {\r\n\tcolor: #a9b7c6;\r\n}\r\n\r\n.token.url {\r\n\tcolor: #287bde;\r\n\ttext-decoration: underline;\r\n}\r\n\r\n.token.function {\r\n\tcolor: #ffc66d;\r\n}\r\n\r\n.token.regex {\r\n\tbackground: #364135;\r\n}\r\n\r\n.token.bold {\r\n\tfont-weight: bold;\r\n}\r\n\r\n.token.italic {\r\n\tfont-style: italic;\r\n}\r\n\r\n.token.inserted {\r\n\tbackground: #294436;\r\n}\r\n\r\n.token.deleted {\r\n\tbackground: #484a4a;\r\n}\r\n\r\ncode.language-css .token.property,\r\ncode.language-css .token.property + .token.punctuation {\r\n\tcolor: #a9b7c6;\r\n}\r\n\r\ncode.language-css .token.id {\r\n\tcolor: #ffc66d;\r\n}\r\n\r\ncode.language-css .token.selector > .token.class,\r\ncode.language-css .token.selector > .token.attribute,\r\ncode.language-css .token.selector > .token.pseudo-class,\r\ncode.language-css .token.selector > .token.pseudo-element {\r\n\tcolor: #ffc66d;\r\n}\r\n";