export default "/**\r\n * Shades of Purple Theme for Prism.js\r\n *\r\n * @author Ahmad Awais <https://twitter.com/MrAhmadAwais/>\r\n * @support Follow/tweet at https://twitter.com/MrAhmadAwais/\r\n */\r\n\r\ncode[class*='language-'],\r\npre[class*='language-'] {\r\n\tcolor: #9efeff;\r\n\tdirection: ltr;\r\n\ttext-align: left;\r\n\twhite-space: pre;\r\n\tword-spacing: normal;\r\n\tword-break: normal;\r\n\r\n\t-moz-tab-size: 4;\r\n\t-o-tab-size: 4;\r\n\ttab-size: 4;\r\n\r\n\t-webkit-hyphens: none;\r\n\t-moz-hyphens: none;\r\n\t-ms-hyphens: none;\r\n\thyphens: none;\r\n\r\n\tfont-family: 'Operator Mono', 'Fira Code', Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;\r\n\tfont-weight: 400;\r\n\tfont-size: 17px;\r\n\tline-height: 25px;\r\n\tletter-spacing: 0.5px;\r\n\ttext-shadow: 0 1px #222245;\r\n}\r\n\r\npre[class*='language-']::-moz-selection,\r\npre[class*='language-'] ::-moz-selection,\r\ncode[class*='language-']::-moz-selection,\r\ncode[class*='language-'] ::-moz-selection,\r\npre[class*='language-']::selection,\r\npre[class*='language-'] ::selection,\r\ncode[class*='language-']::selection,\r\ncode[class*='language-'] ::selection {\r\n\tcolor: inherit;\r\n\tbackground: #a599e9;\r\n}\r\n\r\n/* Code blocks. */\r\npre[class*='language-'] {\r\n\tpadding: 2em;\r\n\tmargin: 0.5em 0;\r\n\toverflow: auto;\r\n}\r\n\r\n:not(pre) > code[class*='language-'],\r\npre[class*='language-'] {\r\n\tbackground: #1e1e3f;\r\n}\r\n\r\n/* Inline code */\r\n:not(pre) > code[class*='language-'] {\r\n\tpadding: 0.1em;\r\n\tborder-radius: 0.3em;\r\n}\r\n\r\n.token {\r\n\tfont-weight: 400;\r\n}\r\n\r\n.token.comment,\r\n.token.prolog,\r\n.token.cdata {\r\n\tcolor: #b362ff;\r\n}\r\n\r\n.token.delimiter,\r\n.token.keyword,\r\n.token.selector,\r\n.token.important,\r\n.token.atrule {\r\n\tcolor: #ff9d00;\r\n}\r\n\r\n.token.operator,\r\n.token.attr-name {\r\n\tcolor: rgb(255, 180, 84);\r\n}\r\n\r\n.token.punctuation {\r\n\tcolor: #ffffff;\r\n}\r\n\r\n.token.boolean {\r\n\tcolor: rgb(255, 98, 140);\r\n}\r\n\r\n.token.tag,\r\n.token.tag .punctuation,\r\n.token.doctype,\r\n.token.builtin {\r\n\tcolor: rgb(255, 157, 0);\r\n}\r\n\r\n.token.entity,\r\n.token.symbol {\r\n\tcolor: #6897bb;\r\n}\r\n\r\n.token.number {\r\n\tcolor: #ff628c;\r\n}\r\n\r\n.token.property,\r\n.token.constant,\r\n.token.variable {\r\n\tcolor: #ff628c;\r\n}\r\n\r\n.token.string,\r\n.token.char {\r\n\tcolor: #a5ff90;\r\n}\r\n\r\n.token.attr-value,\r\n.token.attr-value .punctuation {\r\n\tcolor: #a5c261;\r\n}\r\n\r\n.token.attr-value .punctuation:first-child {\r\n\tcolor: #a9b7c6;\r\n}\r\n\r\n.token.url {\r\n\tcolor: #287bde;\r\n\ttext-decoration: underline;\r\n}\r\n\r\n.token.function {\r\n\tcolor: rgb(250, 208, 0);\r\n}\r\n\r\n.token.regex {\r\n\tbackground: #364135;\r\n}\r\n\r\n.token.bold {\r\n\tfont-weight: bold;\r\n}\r\n\r\n.token.italic {\r\n\tfont-style: italic;\r\n}\r\n\r\n.token.inserted {\r\n\tbackground: #00ff00;\r\n}\r\n\r\n.token.deleted {\r\n\tbackground: #ff000d;\r\n}\r\n\r\ncode.language-css .token.property,\r\ncode.language-css .token.property + .token.punctuation {\r\n\tcolor: #a9b7c6;\r\n}\r\n\r\ncode.language-css .token.id {\r\n\tcolor: #ffc66d;\r\n}\r\n\r\ncode.language-css .token.selector > .token.class,\r\ncode.language-css .token.selector > .token.attribute,\r\ncode.language-css .token.selector > .token.pseudo-class,\r\ncode.language-css .token.selector > .token.pseudo-element {\r\n\tcolor: #ffc66d;\r\n}\r\n\r\n.token.class-name {\r\n\tcolor: #fb94ff;\r\n}\r\n\r\n.token.operator,\r\n.token.entity,\r\n.token.url,\r\n.language-css .token.string,\r\n.style .token.string {\r\n\tbackground: none;\r\n}\r\n\r\npre .line-highlight,\r\npre .line-highlight.line-highlight,\r\npre > code.line-highlight {\r\n\tmargin-top: 36px;\r\n\tbackground: linear-gradient(to right, rgba(179, 98, 255, 0.17), transparent);\r\n}\r\n\r\npre .line-highlight:before,\r\npre > code.line-highlight:before,\r\npre .line-highlight[data-end]:after,\r\npre > code.line-highlight[data-end]:after {\r